<template>
  <div>
    <Page>
      <b-form>
        <b-form-row>
          <b-form-group label="Data Início" class="col-md-auto">
            <date-picker
              v-model="datetime1"
              format="DD-MM-YYYY HH:mm:ss"
              class="mt-1"
              type="datetime"
            ></date-picker>
          </b-form-group>
          <b-form-group label="Data Final" class="col-md-auto">
            <date-picker
              v-model="datetime2"
              format="DD-MM-YYYY HH:mm:ss"
              class="mt-1"
              type="datetime"
            ></date-picker>
          </b-form-group>
          <b-form-group label="Campanhas" class="col-md-auto">
            <b-select plain v-model="queue" :options="queues" size="md">
              <b-select-option value="">Todas as Campanhas</b-select-option>
            </b-select>
          </b-form-group>
          <b-form-group label="Agentes" class="col-md-auto">
            <b-select value="Select state" :options="agents" v-model="agent">
              <b-select-option value="">Todos os Agentes</b-select-option>
            </b-select>
          </b-form-group>
        </b-form-row>
        <hr />
        <b-form-row>
          <ladda-btn
            :loading="loading"
            data-style="expand-left"
            class="btn btn-success"
            @click.native="getCampaingDetailed"
            style="z-index: 10"
            >Visualizar</ladda-btn
          >
        </b-form-row>
      </b-form>
    </Page>
    <Page :hidden="table.length == 0">
      <Table :tableData="table" :filename="filename" :header="header" :legend="legend" />
    </Page>
  </div>
</template>
<script>
import Page from "@/components/Page";
import DatePicker from "vue2-datepicker";
import "@/vendor/libs/vue2-datepicker/locale/pt-br";
import moment from "moment";
import LaddaBtn from "@/vendor/libs/ladda/Ladda";
import axios from "axios";
import Table from "@/components/Table";

export default {
  name: "detailed",
  components: {
    Page,
    DatePicker,
    LaddaBtn,
    Table,
  },
  metaInfo: {
    title: "Relatório de Campanha Detalhado",
  },
  data: () => ({
    datetime1: moment().hour(0).minute(0).second(0).toDate(),
    datetime2: moment().hour(23).minute(59).second(59).toDate(),
    agents: [],
    agent: "",
    queues: [],
    queue: "",
    table: [],
    loading: false,
    filename: "",
    header: [],
    legend: [],
  }),
  mounted() {
    this.$store.commit("setTitle", "Relatório de Campanha Detalhado");
    this.loadQueues();
    this.loadAgents();
  },
  methods: {
    updateHeader() {
      this.header = [];
      this.header.push("<h4>Relatório de Campanha Detalhado</h4>");
      this.header.push(
        `Período ${moment(this.datetime1).format("DD/MM/YYYY")} à ${moment(
          this.datetime2
        ).format("DD/MM/YYYY")} / Hora à ${moment(this.datetime1).format(
          "HH:mm:ss"
        )} até ${moment(this.datetime2).format("HH:mm:ss")}`
      );
      this.header.push(" ");
      this.filename = `Rel_campanha_detalhado ${moment().format("DD_MM_YYYY_HH_mm_ss")}`;

      this.legend = [
        "<h5>Legenda:</h5>",
        "<b>Origem</b> - Origem da chamada;",
        "<b>Campanha</b> - Campanha criada;",
        "<b>Telefone</b> - Identificador de chamada;",
        "<b>Tempo de Espera</b> - Tempo na fila;",
        "<b>Tempo de Atendimento</b> - Chamada em curso;",
        "<b>Finalizados Por</b> - Finalizadas pelo operador ou usuário;",
        `<b>Emitido em:</b> ${moment().format("DD/MM/YYYY HH:mm:ss")}`,
      ];
    },
    loadQueues: function () {
      axios
        .post(`${location.origin}/reportsbackend/queues.php`)
        .then((data) => {
          this.queues = data.data;
        })
        .catch((err) => console.log(err));
    },
    loadAgents: function () {
      axios
        .post(`${location.origin}/reportsbackend/agents.php`)
        .then((data) => {
          this.agents = [];

          data.data.forEach((x) => {
            this.agents.push(x.agent);
          });
        })
        .catch((err) => console.log(err));
    },
    getCampaingDetailed: function () {
      this.loading = true;
      this.table = [];

      const params = new URLSearchParams();
      params.append("time1", moment(this.datetime1).format("YYYY-MM-DD HH:mm:ss"));
      params.append("time2", moment(this.datetime2).format("YYYY-MM-DD HH:mm:ss"));
      params.append("queue", this.queue);
      params.append("agent", this.agent);

      axios
        .post(`${location.origin}/reportsbackend/report_campaing_detail.php`, params)
        .then((data) => {
          if (data.data) {
            if (data.data.length === 0) {
              this.$bvToast.toast("Nenhum registro encontrado", {
                title: "Tecnomafer Report",
                autoHideDelay: 5000,
                appendToast: true,
                toastClass: "bs4-toast bg-danger",
              });
            }

            data.data.forEach((x) => {
              const campaignText = this.queues.find((y) => y.value === x.Campanha);
              if (campaignText) {
                x.Campanha = campaignText.text;
              }
            });
          }
          this.table = data.data;
          this.updateHeader();
        })
        .catch((err) => console.log(err))
        .finally((x) => {
          this.loading = false;
        });
    },
  },
};
</script>
